.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 30vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

.Timeline-site {
	background-color: #e5eaf8;
	text-align: center;
}

.doc-viewer-page {
	background-color: #ffffff;
	padding-top: calc(12vmin);
	padding-bottom: calc(12vmin);
}

.doc-viewer-content {
	margin-left: calc(16vmin);
	margin-right: calc(16vmin);
	text-align: left;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
